body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

header{display:flex;margin-top:0.5em;background:#6d6875}header .content{margin-left:3em;margin-right:3em;padding:0.5em;align-items:flex-start}header h1{color:white}

.searchbar{background-color:#5a606b;border-radius:30px;padding:0.5em;margin-right:4em}.searchbar input{color:white;border:0;outline:0;background:none;width:0;caret-color:transparent;transition:width 0.4s linear}.searchbar span{height:1em;width:1em;float:right;display:flex;justify-content:center;align-items:center;border-radius:50%;color:white;text-decoration:none}.searchbar span:hover{background:#5a606b;color:#e5989b}.searchbar:hover>input{padding:0 10px;width:20rem;caret-color:#e5989b;transition:width 0.4s linear}

.list-inline-item .btn-outine-info{margin-top:1em;border-radius:25px;font-weight:500;border-color:#beb5b7;color:#beb5b7;border-width:2px solid #beb5b7}

section{background-color:#6d6875;padding:0;margin:0}section .container{padding-left:8em}section .carousel-captain{font-size:40px;text-align:center;color:#ffcdb2}

.home{margin:0}

.Singlemovie{display:flex;flex-direction:column;padding:0.5em;margin:0.5em;align-items:center;background-color:#beb5b7;justify-content:space-between;border-radius:0.3em}.Singlemovie img{width:100%}.Singlemovie p{color:black}

.moviesList{display:grid;grid-template:repeat(5, 1fr)/repeat(4, 1fr);background-color:#6d6875}

.nav{display:flex;padding:0.2 0.8em;background-color:#6d6875}.nav a{color:wheat;font-weight:bold;text-decoration:none;list-style:none;margin:1em;padding:0.8em;background-color:#5a606b;cursor:pointer;border-radius:0.6em;transition:width 0.4s linear}.nav a:hover{background-color:whitesmoke;color:#5a606b}

footer{margin-left:2em;margin-right:2em;margin-bottom:auto}

.App{background-color:#6d6875}

